import { Client, IMessage, StompSubscription } from '@stomp/stompjs';
import SockJS from 'sockjs-client';
import { 
  AnyTimerEvent, 
  SaveTimestampRequest, 
  ChangeTargetTimeRequest 
} from '../types/timer';

/**
 * WebSocket (STOMP)  
 *     
 */

export type EventHandler<T = AnyTimerEvent> = (event: T) => void;
export type ConnectionStatusHandler = (connected: boolean) => void;

export class WebSocketService {
  private client: Client | null = null;
  private subscriptions: Map<string, StompSubscription> = new Map();
  private eventHandlers: Map<string, EventHandler[]> = new Map();
  private connectionStatusHandlers: ConnectionStatusHandler[] = [];
  private isConnected = false;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;

  constructor() {
    this.setupClient();
  }

  /**
   * STOMP   
   */
  private setupClient(): void {
    this.client = new Client({
      // SockJS  WebSocket  (Vite  )
      webSocketFactory: () => new SockJS('/ws'),
      
      //  
      connectHeaders: {
        // TODO:      
        // Authorization: `Bearer ${token}`
      },

      //  
      debug: (str) => {
        console.log(' STOMP:', str);
      },

      //  
      reconnectDelay: 5000, // 5   
      heartbeatIncoming: 4000, //    
      heartbeatOutgoing: 4000, //    

      //   
      onConnect: (frame) => {
        console.log(' WebSocket  :', frame);
        this.isConnected = true;
        this.reconnectAttempts = 0;
        this.notifyConnectionStatus(true);
      },

      //   
      onStompError: (frame) => {
        console.error(' STOMP :', frame.headers['message']);
        console.error(' :', frame.body);
        this.isConnected = false;
        this.notifyConnectionStatus(false);
      },

      //   
      onDisconnect: (frame) => {
        console.log(' WebSocket  :', frame);
        this.isConnected = false;
        this.notifyConnectionStatus(false);
      },

      // WebSocket   
      onWebSocketError: (error) => {
        console.error(' WebSocket :', error);
        this.handleReconnect();
      }
    });
  }

  /**
   * WebSocket  
   */
  connect(): void {
    if (this.client && !this.isConnected) {
      console.log(' WebSocket  ...');
      this.client.activate();
    }
  }

  /**
   * WebSocket  
   */
  disconnect(): void {
    if (this.client && this.isConnected) {
      console.log(' WebSocket  ...');
      
      //   
      this.subscriptions.forEach((subscription) => {
        subscription.unsubscribe();
      });
      this.subscriptions.clear();
      
      //  
      this.client.deactivate();
    }
  }

  /**
   *  
   */
  private handleReconnect(): void {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      console.log(`   ${this.reconnectAttempts}/${this.maxReconnectAttempts}...`);
      
      setTimeout(() => {
        if (!this.isConnected) {
          this.connect();
        }
      }, 5000 * this.reconnectAttempts); //  
    } else {
      console.error('     ');
    }
  }

  /**
   *    
   * @param timerId  ID
   * @param userId  ID
   */
  subscribeToTimer(timerId: string, userId: string): void {
    if (!this.client || !this.isConnected) {
      console.warn(' WebSocket  .');
      return;
    }

    const destination = `/topic/timer/${timerId}`;
    
    //    
    if (this.subscriptions.has(destination)) {
      console.log('    :', timerId);
      return;
    }

    console.log('   :', timerId);
    
    const subscription = this.client.subscribe(destination, (message: IMessage) => {
      try {
        console.log(' WebSocket   :', message.body);
        const event: AnyTimerEvent = JSON.parse(message.body);
        console.log('   :', event.eventType, event);
        
        //   
        this.notifyEventHandlers(event.eventType, event);
        this.notifyEventHandlers('*', event); //    
      } catch (error) {
        console.error('   :', error, 'Original message:', message.body);
      }
    }, {
      userId: userId //  userId 
    });

    this.subscriptions.set(destination, subscription);
  }

  /**
   *   
   * @param timerId  ID
   */
  unsubscribeFromTimer(timerId: string): void {
    const destination = `/topic/timer/${timerId}`;
    const subscription = this.subscriptions.get(destination);
    
    if (subscription) {
      console.log('   :', timerId);
      subscription.unsubscribe();
      this.subscriptions.delete(destination);
    }
  }

  /**
   *    
   * @param timerId  ID
   * @param request   
   */
  saveTimestamp(timerId: string, request: SaveTimestampRequest): void {
    if (!this.client || !this.isConnected) {
      console.warn(' WebSocket  .');
      return;
    }

    const destination = `/app/timer/${timerId}/save`;
    console.log('   :', destination, request);
    
    this.client.publish({
      destination,
      body: JSON.stringify(request),
      headers: {
        userId: request.userId // userId  
      }
    });
  }

  /**
   *     
   * @param timerId  ID
   * @param request   
   */
  changeTargetTime(timerId: string, request: ChangeTargetTimeRequest): void {
    if (!this.client || !this.isConnected) {
      console.warn(' WebSocket  .');
      return;
    }

    const destination = `/app/timer/${timerId}/change-target`;
    console.log('    :', destination, request);
    
    this.client.publish({
      destination,
      body: JSON.stringify(request),
      headers: {
        userId: request.changedBy // userId  
      }
    });
  }

  /**
   *    
   * @param timerId  ID
   * @param userId  ID
   */
  completeTimer(timerId: string, userId: string): void {
    if (!this.client || !this.isConnected) {
      console.warn(' WebSocket  .');
      return;
    }

    const destination = `/app/timer/${timerId}/complete`;
    console.log('   :', destination, { userId });
    
    this.client.publish({
      destination,
      body: JSON.stringify({}),
      headers: {
        userId: userId // userId  
      }
    });
  }

  /**
   *   
   * @param eventType   ('*'  )
   * @param handler   
   */
  addEventListener(eventType: string, handler: EventHandler): void {
    if (!this.eventHandlers.has(eventType)) {
      this.eventHandlers.set(eventType, []);
    }
    this.eventHandlers.get(eventType)!.push(handler);
  }

  /**
   *   
   * @param eventType  
   * @param handler   
   */
  removeEventListener(eventType: string, handler: EventHandler): void {
    const handlers = this.eventHandlers.get(eventType);
    if (handlers) {
      const index = handlers.indexOf(handler);
      if (index > -1) {
        handlers.splice(index, 1);
      }
    }
  }

  /**
   *     
   * @param handler    
   */
  addConnectionStatusListener(handler: ConnectionStatusHandler): void {
    this.connectionStatusHandlers.push(handler);
  }

  /**
   *     
   * @param handler  
   */
  removeConnectionStatusListener(handler: ConnectionStatusHandler): void {
    const index = this.connectionStatusHandlers.indexOf(handler);
    if (index > -1) {
      this.connectionStatusHandlers.splice(index, 1);
    }
  }

  /**
   *    
   */
  private notifyEventHandlers(eventType: string, event: AnyTimerEvent): void {
    const handlers = this.eventHandlers.get(eventType);
    if (handlers) {
      handlers.forEach(handler => {
        try {
          handler(event);
        } catch (error) {
          console.error('   :', error);
        }
      });
    }
  }

  /**
   *    
   */
  private notifyConnectionStatus(connected: boolean): void {
    this.connectionStatusHandlers.forEach(handler => {
      try {
        handler(connected);
      } catch (error) {
        console.error('    :', error);
      }
    });
  }

  /**
   *    
   */
  get connected(): boolean {
    return this.isConnected;
  }
}

//   
export const webSocketService = new WebSocketService();
