import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  List,
  ListItem,
  Chip,
  Divider,
  CircularProgress,
  Alert,
  Stack
} from '@mui/material';
import {
  AccessTime,
  Person,
  History
} from '@mui/icons-material';
import { TimestampEntry } from '../types/timer';
import { TimerApiService } from '../services/api';

/**
 *   
 *    
 */

interface TimestampListProps {
  /**  ID */
  timerId: string;
  /**   ID */
  userId: string;
  /**       */
  refreshTrigger?: number;
}

export const TimestampList: React.FC<TimestampListProps> = ({
  timerId,
  userId,
  refreshTrigger = 0
}) => {
  const [timestamps, setTimestamps] = useState<TimestampEntry[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  /**
   *    ()
   */
  const loadTimestamps = async () => {
    if (!timerId || !userId) return;
    
    setLoading(true);
    setError(null);
    
    try {
      //   API ,    
      let timestampList;
      try {
        timestampList = await TimerApiService.getUserTimerHistory(timerId, userId);
      } catch (userApiError) {
        console.warn(' API ,   :', userApiError);
        const allTimestamps = await TimerApiService.getTimerHistory(timerId);
        timestampList = allTimestamps.filter(ts => ts.userId === userId);
      }
      setTimestamps(timestampList);
    } catch (err) {
      console.error('   :', err);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  /**
   *     refreshTrigger    
   */
  useEffect(() => {
    loadTimestamps();
  }, [timerId, userId, refreshTrigger]);

  /**
   *  MM:SS  
   */
  const formatDuration = (milliseconds: number): string => {
    if (!milliseconds || isNaN(milliseconds) || milliseconds < 0) {
      return '00:00';
    }
    const totalSeconds = Math.floor(milliseconds / 1000);
    const hours = Math.floor(totalSeconds / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const seconds = totalSeconds % 60;
    
    if (hours > 0) {
      return `${hours}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    }
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  };

  /**
   *       
   * (      )
   */
  const calculateActualRemainingTime = (timestamp: TimestampEntry): number => {
    const savedAt = new Date(timestamp.createdAt).getTime();
    const targetTime = new Date(timestamp.targetTime).getTime();
    const remainingTime = targetTime - savedAt;
    return Math.max(0, remainingTime);
  };

  /**
   *    
   */
  const formatDate = (dateString: string): string => {
    const date = new Date(dateString);
    return date.toLocaleString('ko-KR', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  };

  /**
   *  ID   
   */
  const formatUserId = (userId: string): string => {
    if (userId.length <= 8) return userId;
    return `${userId.substring(0, 4)}...${userId.substring(userId.length - 4)}`;
  };

  if (loading) {
    return (
      <Card elevation={2} sx={{ mt: 2 }}>
        <CardContent>
          <Box display="flex" justifyContent="center" alignItems="center" py={3}>
            <CircularProgress size={24} />
            <Typography variant="body2" color="text.secondary" ml={2}>
                 ...
            </Typography>
          </Box>
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card elevation={2} sx={{ mt: 2 }}>
        <CardContent>
          <Alert severity="error">{error}</Alert>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card elevation={2} sx={{ mt: 2 }}>
      <CardContent>
        {/*  */}
        <Stack direction="row" alignItems="center" spacing={1} mb={2}>
          <History color="primary" />
          <Typography variant="h6" color="primary">
             
          </Typography>
          <Chip 
            label={`${timestamps.length}`} 
            size="small" 
            color="primary" 
            variant="outlined" 
          />
        </Stack>

        {timestamps.length === 0 ? (
          <Box textAlign="center" py={3}>
            <Typography variant="body2" color="text.secondary">
                 .
            </Typography>
            <Typography variant="caption" color="text.secondary" display="block" mt={1}>
                 ''     .
            </Typography>
          </Box>
        ) : (
          <List disablePadding>
            {timestamps.map((timestamp, index) => (
              <React.Fragment key={timestamp.id || index}>
                <ListItem
                  sx={{
                    px: 0,
                    py: 1.5,
                    '&:hover': {
                      backgroundColor: 'rgba(0, 0, 0, 0.04)'
                    },
                    flexDirection: 'column',
                    alignItems: 'stretch'
                  }}
                >
                  {/* Primary content */}
                  <Stack direction="row" alignItems="center" spacing={1} mb={0.5}>
                    <AccessTime fontSize="small" color="action" />
                    <Typography variant="body1" fontWeight="medium">
                        : {formatDate(timestamp.createdAt)}
                    </Typography>
                    <Chip
                      icon={<Person />}
                      label={formatUserId(timestamp.userId)}
                      size="small"
                      variant="outlined"
                      sx={{ ml: 'auto' }}
                    />
                  </Stack>

                  {/* Secondary content */}
                  <Stack spacing={0.5}>
                    <Stack direction="row" justifyContent="space-between" alignItems="center">
                      <Typography variant="caption" color="text.secondary">
                          : {formatDate(timestamp.createdAt)}
                      </Typography>
                      <Typography variant="caption" color="text.secondary">
                          : {formatDuration(calculateActualRemainingTime(timestamp))}
                      </Typography>
                    </Stack>
                    <Typography variant="caption" color="text.secondary">
                        : {new Date(timestamp.targetTime).toLocaleString('ko-KR', {
                        month: 'short',
                        day: 'numeric',
                        hour: '2-digit',
                        minute: '2-digit'
                      })}
                    </Typography>
                  </Stack>
                </ListItem>
                {index < timestamps.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        )}
      </CardContent>
    </Card>
  );
};
