import { useState, useEffect, useCallback } from 'react';
import {
  ThemeProvider,
  createTheme,
  CssBaseline,
  Container,
  Typography,
  Box,
  Fab,
  Snackbar,
  Alert,
  Chip,
  Stack,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField
} from '@mui/material';
import { Add, Wifi, WifiOff } from '@mui/icons-material';

import { TimerDisplay } from './components/TimerDisplay';
import { TimerCreator } from './components/TimerCreator';
import { TimestampList } from './components/TimestampList';
import { useTimer } from './hooks/useTimer';
import { TimerApiService } from './services/api';

/**
 *   
 *      UI 
 */

// Material-UI  
const theme = createTheme({
  palette: {
    primary: {
      main: '#667eea',
    },
    secondary: {
      main: '#f093fb',
    },
    background: {
      default: '#f5f7fa',
    },
  },
  typography: {
    fontFamily: '"Noto Sans KR", "Roboto", "Helvetica", "Arial", sans-serif',
  },
  shape: {
    borderRadius: 12,
  },
});

function App() {
  //  
  const [showCreator, setShowCreator] = useState(() => {
    // sessionStorage   ID  Creator 
    const existingTimerId = sessionStorage.getItem('kb-echotimer-current-timer-id');
    return !existingTimerId;
  });
  const [userId] = useState(() => {
    // sessionStorage   ID ,   
    const existingUserId = sessionStorage.getItem('kb-echotimer-user-id');
    if (existingUserId) {
      return existingUserId;
    }
    const newUserId = `user-${Date.now()}`;
    sessionStorage.setItem('kb-echotimer-user-id', newUserId);
    return newUserId;
  });
  const [currentTimerId, setCurrentTimerId] = useState<string | null>(() => {
    // sessionStorage   ID 
    return sessionStorage.getItem('kb-echotimer-current-timer-id');
  });
  const [snackbar, setSnackbar] = useState<{
    open: boolean;
    message: string;
    severity: 'success' | 'error' | 'info' | 'warning';
  }>({
    open: false,
    message: '',
    severity: 'info'
  });
  const [timestampRefreshTrigger, setTimestampRefreshTrigger] = useState(0);
  const [showTargetTimeDialog, setShowTargetTimeDialog] = useState(false);
  const [newTargetTime, setNewTargetTime] = useState('');
  //     WebSocket  

  //    
  useEffect(() => {
    if ('Notification' in window && Notification.permission === 'default') {
      Notification.requestPermission().then(permission => {
        console.log(' :', permission);
      });
    }
  }, []);

  // URL  ID  (  )
  useEffect(() => {
    const path = window.location.pathname;
    const urlParams = new URLSearchParams(window.location.search);
    
    // /timer/{shareToken}  URL 
    const timerMatch = path.match(/^\/timer\/(.+)$/);
    if (timerMatch) {
      const shareToken = timerMatch[1];
      setCurrentTimerId(shareToken);
      sessionStorage.setItem('kb-echotimer-current-timer-id', shareToken);
      setShowCreator(false);
      return;
    }
    
    //    ( )
    const timerIdFromUrl = urlParams.get('timer');
    if (timerIdFromUrl) {
      setCurrentTimerId(timerIdFromUrl);
      sessionStorage.setItem('kb-echotimer-current-timer-id', timerIdFromUrl);
      setShowCreator(false);
    }
  }, []);

  /**
   *    
   */
  const handleTimerCompleted = useCallback(() => {
    showSnackbar('  !', 'success');
    
    //   (  )
    if ('Notification' in window && Notification.permission === 'granted') {
      new Notification('KB EchoTimer', {
        body: ' ! ',
        icon: '/favicon.svg'
      });
    }
  }, []);

  /**
   *      ( )
   */
  const handleSharedTimerAccessed = useCallback((accessedUserId: string) => {
    showSnackbar(`${accessedUserId}   ! `, 'info');
    
    //   (  )
    if ('Notification' in window && Notification.permission === 'granted') {
      new Notification('KB EchoTimer', {
        body: `${accessedUserId}   !`,
        icon: '/favicon.svg'
      });
    }
  }, []);

  //   
  const {
    timer,
    loading,
    error,
    connected,
    createTimer,
    loadTimer,
    saveTimestamp,
    completeTimer,
    remainingSeconds,
    isCompleted,
    progress
  } = useTimer({
    timerId: currentTimerId || undefined,
    userId,
    autoConnect: true,
    isShareToken: !!(currentTimerId && window.location.pathname.startsWith('/timer/')), //    
    onTimerCompleted: handleTimerCompleted, //    
    onSharedTimerAccessed: handleSharedTimerAccessed //     
  });

  //      WebSocket   ( )

  /**
   *   
   */
  const showSnackbar = (
    message: string, 
    severity: 'success' | 'error' | 'info' | 'warning' = 'info'
  ) => {
    setSnackbar({ open: true, message, severity });
  };

  /**
   *   
   */
  const handleCreateTimer = async (targetTimeSeconds: number) => {
    try {
      const newTimer = await createTimer(targetTimeSeconds);
      setCurrentTimerId(newTimer.timerId);
      sessionStorage.setItem('kb-echotimer-current-timer-id', newTimer.timerId);
      setShowCreator(false);
      showSnackbar(' !', 'success');
      
      // URL  ( )
      if (newTimer?.shareToken) {
        const token = newTimer.shareToken.replace('/timer/', '');
        const newUrl = `${window.location.origin}/timer/${token}`;
        window.history.pushState({}, '', newUrl);
        // URL  sessionStorage 
        sessionStorage.setItem('kb-echotimer-current-timer-id', token);
      }
    } catch (err) {
      showSnackbar('  .', 'error');
    }
  };

  /**
   *   
   */
  const handleSaveTimestamp = async () => {
    try {
      await saveTimestamp();
      showSnackbar(' !', 'success');
      //    
      setTimestampRefreshTrigger(prev => prev + 1);
    } catch (err) {
      showSnackbar('  .', 'error');
    }
  };

  /**
   *  
   */
  const handleShare = async () => {
    if (!timer?.shareToken) return;
    
    // shareToken /timer/     
    const token = timer.shareToken.replace('/timer/', '');
    const shareUrl = `${window.location.origin}/timer/${token}`;
    
    try {
      //  API 
      await navigator.clipboard.writeText(shareUrl);
      showSnackbar('   !', 'success');
    } catch (err) {
      // :  
      const textArea = document.createElement('textarea');
      textArea.value = shareUrl;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      showSnackbar('  !', 'success');
    }
  };

  /**
   *   
   */
  const handleCompleteTimer = async () => {
    try {
      await completeTimer();
      showSnackbar(' !', 'success');
    } catch (err) {
      showSnackbar('   .', 'error');
    }
  };

  /**
   *   
   */
  const handleNewTimer = () => {
    setShowCreator(true);
    setCurrentTimerId(null);
    window.history.pushState({}, '', window.location.origin);
  };

  /**
   *     
   */
  const handleOpenTargetTimeDialog = () => {
    if (timer?.targetTime) {
      //       
      const currentTarget = new Date(timer.targetTime);
      const year = currentTarget.getFullYear();
      const month = String(currentTarget.getMonth() + 1).padStart(2, '0');
      const day = String(currentTarget.getDate()).padStart(2, '0');
      const hours = String(currentTarget.getHours()).padStart(2, '0');
      const minutes = String(currentTarget.getMinutes()).padStart(2, '0');
      
      setNewTargetTime(`${year}-${month}-${day}T${hours}:${minutes}`);
    }
    setShowTargetTimeDialog(true);
  };

  /**
   *    
   */
  const handleChangeTargetTime = async () => {
    if (!newTargetTime || !timer) return;

    try {
      const targetTime = new Date(newTargetTime);
      const now = new Date();
      
      if (targetTime <= now) {
        showSnackbar('     .', 'error');
        return;
      }

      // API 
      await TimerApiService.changeTargetTime(timer.timerId, {
        newTargetTime: targetTime.toISOString(),
        changedBy: userId
      });

      showSnackbar('  !', 'success');
      setShowTargetTimeDialog(false);
      setNewTargetTime('');
      
      //    
      if (currentTimerId) {
        await loadTimer(currentTimerId);
      } else if (timer.timerId) {
        // currentTimerId  timer.timerId 
        await loadTimer(timer.timerId);
      }
      
      //   
      setTimestampRefreshTrigger(prev => prev + 1);
    } catch (err) {
      console.error('   :', err);
      showSnackbar('   .', 'error');
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      
      {/*   */}
      <Container maxWidth="md" sx={{ py: 4 }}>
        {/*   */}
        <Box textAlign="center" mb={4}>
          <Typography variant="h4" component="h1" gutterBottom sx={{ fontWeight: 'bold' }}>
             KB EchoTimer
          </Typography>
          
          {/*    */}
          <Chip
            icon={connected ? <Wifi /> : <WifiOff />}
            label={connected ? '' : ' '}
            color={connected ? 'success' : 'error'}
            size="small"
            variant="outlined"
          />
        </Box>
        {showCreator ? (
          /*    */
          <TimerCreator
            loading={loading}
            error={error}
            onCreateTimer={handleCreateTimer}
          />
        ) : timer ? (
          /*    */
          <Box>
            <TimerDisplay
              remainingSeconds={remainingSeconds}
              isCompleted={isCompleted}
              progress={progress}
              onlineUserCount={timer.onlineUserCount}
              userRole={timer.userRole}
              timerId={timer.timerId}
              onSave={handleSaveTimestamp}
              onShare={handleShare}
              onComplete={handleCompleteTimer}
              onEditTargetTime={handleOpenTargetTimeDialog}
            />
            
            {/*   */}
            <Box mt={3} textAlign="center">
              <Stack spacing={1} alignItems="center">
                <Typography variant="body2" color="text.secondary">
                   : {timer.ownerId}
                </Typography>
                <Typography variant="body2" color="primary.main" fontWeight="medium">
                   : {userId}
                </Typography>
              </Stack>
              
              <Typography variant="body2" color="text.secondary" mt={1}>
                 : {new Date(timer.targetTime).toLocaleString('ko-KR')}
              </Typography>
            </Box>
            
            {/*   */}
            <TimestampList 
              timerId={timer.timerId} 
              userId={userId}
              refreshTrigger={timestampRefreshTrigger}
            />
          </Box>
        ) : (
          /*   */
          <Box textAlign="center" py={8}>
            <Typography variant="h6" color="text.secondary">
                ...
            </Typography>
          </Box>
        )}
      </Container>

      {/*    FAB */}
      {!showCreator && (
        <Fab
          color="primary"
          aria-label=" "
          onClick={handleNewTimer}
          sx={{
            position: 'fixed',
            bottom: 24,
            right: 24,
          }}
        >
          <Add />
        </Fab>
      )}

      {/*     */}
      <Dialog
        open={showTargetTimeDialog}
        onClose={() => setShowTargetTimeDialog(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          <TextField
            label="  "
            type="datetime-local"
            value={newTargetTime}
            onChange={(e) => setNewTargetTime(e.target.value)}
            fullWidth
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
            helperText="     ."
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowTargetTimeDialog(false)}>
            
          </Button>
          <Button 
            onClick={handleChangeTargetTime}
            variant="contained"
            disabled={!newTargetTime}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  () */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          variant="filled"
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </ThemeProvider>
  );
}

export default App;
