import axios, { AxiosResponse } from 'axios';
import { 
  TimerResponse, 
  CreateTimerRequest, 
  SaveTimestampRequest, 
  ChangeTargetTimeRequest,
  TimestampEntry 
} from '../types/timer';

/**
 * REST API  
 *  HTTP  
 */

// Axios   ( )
const apiClient = axios.create({
  baseURL: '/api/v1', // Vite    
  timeout: 10000, // 10 
  headers: {
    'Content-Type': 'application/json',
  },
});

//   (     )
apiClient.interceptors.request.use(
  (config) => {
    console.log(` API : ${config.method?.toUpperCase()} ${config.url}`);
    // TODO:     
    // config.headers.Authorization = `Bearer ${token}`;
    return config;
  },
  (error) => {
    console.error(' API  :', error);
    return Promise.reject(error);
  }
);

//   (   )
apiClient.interceptors.response.use(
  (response) => {
    console.log(` API : ${response.status} ${response.config.url}`);
    return response;
  },
  (error) => {
    console.error(' API  :', error.response?.data || error.message);
    return Promise.reject(error);
  }
);

/**
 *  API 
 */
export class TimerApiService {
  
  /**
   *   
   * @param request    
   * @returns   
   */
  static async createTimer(request: CreateTimerRequest): Promise<TimerResponse> {
    const response: AxiosResponse<TimerResponse> = await apiClient.post('/timers', request);
    return response.data;
  }

  /**
   *    
   * @param timerId  ID
   * @param userId  ID ()
   * @returns  
   */
  static async getTimerInfo(timerId: string, userId?: string): Promise<TimerResponse> {
    const params = userId ? { userId } : {};
    const response: AxiosResponse<TimerResponse> = await apiClient.get(`/timers/${timerId}`, { params });
    return response.data;
  }

  /**
   *     
   * @param shareToken  
   * @param userId  ID ()
   * @returns  
   */
  static async getTimerInfoByShareToken(shareToken: string, userId?: string): Promise<TimerResponse> {
    const params = userId ? { userId } : {};
    const response: AxiosResponse<TimerResponse> = await apiClient.get(`/timers/shared/${shareToken}`, { params });
    return response.data;
  }

  /**
   *    
   * @param timerId  ID
   * @param request     
   * @returns   
   */
  static async changeTargetTime(timerId: string, request: ChangeTargetTimeRequest): Promise<TimerResponse> {
    const response: AxiosResponse<TimerResponse> = await apiClient.put(`/timers/${timerId}/target-time`, request);
    return response.data;
  }

  /**
   *  
   * @param timerId  ID
   * @param request    
   * @returns   
   */
  static async saveTimestamp(timerId: string, request: SaveTimestampRequest): Promise<TimestampEntry> {
    const response: AxiosResponse<TimestampEntry> = await apiClient.post(`/timers/${timerId}/timestamps`, request);
    return response.data;
  }

  /**
   *   
   * @param timerId  ID
   * @returns   
   */
  static async getTimerHistory(timerId: string): Promise<TimestampEntry[]> {
    const response: AxiosResponse<TimestampEntry[]> = await apiClient.get(`/timers/${timerId}/history`);
    return response.data;
  }

  /**
   *     
   * @param timerId  ID
   * @param userId  ID
   * @returns   
   */
  static async getUserTimerHistory(timerId: string, userId: string): Promise<TimestampEntry[]> {
    const response: AxiosResponse<TimestampEntry[]> = await apiClient.get(`/timers/${timerId}/history/${userId}`);
    return response.data;
  }

  // ==================== Redis  API ====================
  
  /**
   *  Redis  
   */
  static async getRedisKeys(pattern: string = '*'): Promise<string[]> {
    const response: AxiosResponse<string[]> = await apiClient.get(`/debug/redis/keys?pattern=${pattern}`);
    return response.data;
  }

  /**
   *      
   */
  static async getTimerUsers(timerId: string): Promise<any> {
    const response: AxiosResponse<any> = await apiClient.get(`/debug/redis/timer/${timerId}/users`);
    return response.data;
  }

  /**
   *      
   */
  static async getAllTimerUsers(): Promise<any[]> {
    const response: AxiosResponse<any[]> = await apiClient.get(`/debug/redis/timers/all-users`);
    return response.data;
  }

  /**
   * Redis  
   */
  static async getRedisStats(): Promise<any> {
    const response: AxiosResponse<any> = await apiClient.get(`/debug/redis/stats`);
    return response.data;
  }

  /**
   *  Redis   
   */
  static async getRedisKeyValue(key: string): Promise<any> {
    const response: AxiosResponse<any> = await apiClient.get(`/debug/redis/key/${encodeURIComponent(key)}`);
    return response.data;
  }

  /**
   *  Redis  
   */
  static async deleteRedisKey(key: string): Promise<any> {
    const response: AxiosResponse<any> = await apiClient.delete(`/debug/redis/key/${encodeURIComponent(key)}`);
    return response.data;
  }

  /**
   *     
   */
  static async removeUserFromTimer(timerId: string, userId: string): Promise<any> {
    const response: AxiosResponse<any> = await apiClient.delete(`/debug/redis/timer/${timerId}/user/${userId}`);
    return response.data;
  }
}

/**
 * API   
 */
export class ApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public data?: any
  ) {
    super(message);
    this.name = 'ApiError';
  }

  static fromAxiosError(error: any): ApiError {
    if (error.response) {
      //     
      return new ApiError(
        error.response.data?.message || '  .',
        error.response.status,
        error.response.data
      );
    } else if (error.request) {
      //     
      return new ApiError('   .  .');
    } else {
      //     
      return new ApiError('    .');
    }
  }
}

export default apiClient;
