import React from 'react';
import {
  Box,
  Typography,
  LinearProgress,
  Chip,
  Card,
  CardContent,
  Stack,
  IconButton,
  Tooltip
} from '@mui/material';
import {
  Stop,
  Save,
  Share,
  People,
  Edit
} from '@mui/icons-material';

/**
 *   
 *      
 */

interface TimerDisplayProps {
  /**   () */
  remainingSeconds: number;
  /**   */
  isCompleted: boolean;
  /**  (0-100) */
  progress: number;
  /**    */
  onlineUserCount?: number;
  /**   */
  userRole?: 'OWNER' | 'VIEWER';
  /**  ID () */
  timerId?: string;
  
  /**   */
  onSave?: () => void;
  onShare?: () => void;
  onComplete?: () => void;
  onEditTargetTime?: () => void;
}

export const TimerDisplay: React.FC<TimerDisplayProps> = ({
  remainingSeconds,
  isCompleted,
  progress,
  onlineUserCount = 0,
  userRole = 'VIEWER',
  timerId,
  onSave,
  onShare,
  onComplete,
  onEditTargetTime
}) => {
  
  /**
   *  MM:SS  
   */
  const formatTime = (seconds: number): string => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
  };

  /**
   *     
   */
  const getProgressColor = (): 'primary' | 'warning' | 'error' => {
    if (isCompleted) return 'primary'; //   
    if (remainingSeconds > 300) return 'primary'; // 5    
    if (remainingSeconds > 60) return 'warning'; // 1    
    return 'error'; // 1    
  };

  /**
   *   
   */
  const getStatusMessage = (): string => {
    if (isCompleted) return '  !';
    if (remainingSeconds < 60) return ' 1  ';
    if (remainingSeconds < 300) return ' 5  ';
    return '  ';
  };

  return (
    <Card 
      elevation={3}
      sx={{ 
        maxWidth: 500, 
        mx: 'auto',
        background: isCompleted 
          ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
          : 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)'
      }}
    >
      <CardContent sx={{ p: 3 }}>
        {/*   */}
        <Stack direction="row" justifyContent="space-between" alignItems="center" mb={2}>
          <Typography variant="h6" color="white" fontWeight="bold">
            {getStatusMessage()}
          </Typography>
          
          {/*    */}
          <Chip
            icon={<People />}
            label={`${onlineUserCount} `}
            size="small"
            sx={{ 
              backgroundColor: 'rgba(255,255,255,0.2)',
              color: 'white',
              '& .MuiChip-icon': { color: 'white' }
            }}
          />
        </Stack>

        {/*    */}
        <Box textAlign="center" mb={3}>
          <Typography 
            variant="h1" 
            component="div"
            sx={{ 
              fontSize: { xs: '3rem', sm: '4rem' },
              fontWeight: 'bold',
              color: 'white',
              textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
              fontFamily: 'monospace'
            }}
          >
            {formatTime(remainingSeconds)}
          </Typography>
          
          {/*     -   */}
        </Box>

        {/*   */}
        <Box mb={3}>
          <LinearProgress
            variant="determinate"
            value={progress}
            color={getProgressColor()}
            sx={{
              height: 12,
              borderRadius: 6,
              backgroundColor: 'rgba(255,255,255,0.2)',
              '& .MuiLinearProgress-bar': {
                borderRadius: 6,
              }
            }}
          />
          {/*    - NaN  */}
        </Box>

        {/*   */}
        <Stack direction="row" justifyContent="center" spacing={1}>
          {/*   */}
          <Tooltip title="  ">
            <IconButton
              onClick={onSave}
              disabled={isCompleted}
              sx={{ 
                backgroundColor: 'rgba(255,255,255,0.1)',
                color: 'white',
                '&:hover': { backgroundColor: 'rgba(255,255,255,0.2)' },
                '&:disabled': { opacity: 0.5 }
              }}
            >
              <Save />
            </IconButton>
          </Tooltip>

          {/*  */}
          <Tooltip title=" ">
            <IconButton
              onClick={onShare}
              sx={{ 
                backgroundColor: 'rgba(255,255,255,0.1)',
                color: 'white',
                '&:hover': { backgroundColor: 'rgba(255,255,255,0.2)' }
              }}
            >
              <Share />
            </IconButton>
          </Tooltip>

          {/*  (Owner) */}
          {userRole === 'OWNER' && (
            <Tooltip title=" ">
              <IconButton
                onClick={onComplete}
                disabled={isCompleted}
                sx={{ 
                  backgroundColor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': { backgroundColor: 'rgba(255,255,255,0.2)' },
                  '&:disabled': { opacity: 0.5 }
                }}
              >
                <Stop />
              </IconButton>
            </Tooltip>
          )}

          {/*    (Owner,   ) */}
          {userRole === 'OWNER' && !isCompleted && onEditTargetTime && (
            <Tooltip title="  ">
              <IconButton
                onClick={() => {
                  console.log('      - userRole:', userRole, 'isCompleted:', isCompleted);
                  onEditTargetTime();
                }}
                sx={{ 
                  backgroundColor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': { backgroundColor: 'rgba(255,255,255,0.2)' }
                }}
              >
                <Edit />
              </IconButton>
            </Tooltip>
          )}
        </Stack>

        {/*  ID () */}
        {timerId && (
          <Typography 
            variant="caption" 
            color="rgba(255,255,255,0.6)" 
            display="block" 
            textAlign="center" 
            mt={2}
            sx={{ fontFamily: 'monospace' }}
          >
            Timer ID: {timerId}
          </Typography>
        )}
      </CardContent>
    </Card>
  );
};
